body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Arial, sans-serif;
  }
  
  .login-page {
    background-image: url('https://path/to/your/background/image.jpg');
    background-size: cover;
    background-position: center;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background: rgba(255, 255, 255, 0.8);
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
    color: #000;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .nav-links li a {
    text-decoration: none;
    color: #000;
  }
  
  .search-btn {
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .login-container {
    background: rgba(255, 255, 255, 0.9);
    padding: 40px;
    margin: auto;
    width: 400px;
    border-radius: 10px;
    text-align: center;
  }
  
  .login-container h1 {
    margin-bottom: 20px;
  }
  
  .login-container input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-container .forgot-password {
    display: block;
    margin: 10px 0;
    text-align: right;
    text-decoration: none;
    color: #000;
  }
  
  .login-container .login-btn {
    width: 100%;
    padding: 10px;
    background: #007bff;
    border: none;
    color: #fff;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .signup-text {
    margin: 20px 0;
  }
  
  .signup-text a {
    text-decoration: none;
    color: #007bff;
  }
  
  .social-login {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .social-btn {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
  }
  
  .social-btn.google {
    background: #4285f4;
    color: #fff;
  }
  
  .social-btn.facebook {
    background: #3b5998;
    color: #fff;
  }
  
  .social-btn.apple {
    background: #000;
    color: #fff;
  }
  
  footer {
    background: rgba(255, 255, 255, 0.8);
    padding: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  
  .contact-info, .quick-links {
    width: 200px;
  }
  
  .quick-links ul {
    list-style: none;
    padding: 0;
  }
  
  .quick-links ul li a {
    text-decoration: none;
    color: #000;
  }
  